@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);

td,th{
    z-index: 0;
    vertical-align:top; 
    border: 1px solid black;
    height: 40px;
    min-width: 100px;
    text-align: left;
    align-items: center;
    background-color: grey;
    font-weight: 200;
    color:white;
}
th{
    background-color: #657D95;
    z-index: 2;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
}
table{
    border-spacing: 0px;
}
body{
    font-family: 'Inter';
    margin: 0 auto;
}
